.App {
  /*text-align: center;*/
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #6ec4f7;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

.main-jumbotron{
  background-color:#669ed7;
  padding:0;
  margin:3rem 0;
}

.main-jumbotron-div-content{
  background-image: url(./background.jpg);
  max-width:1200px;
  min-height:600px;
  display:flex;
  flex-direction: row;
  margin:0 auto;
}

.main-jumbotron-col-flex{
  display:flex;
  flex-direction: column;
  justify-content: center;
}

.main-footer{
  display:flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
}

.main-footer-logo{
  width:100px;
  height:20px;
  margin:0 auto;
}

